import React, { useEffect, useState } from "react";

export default function CARD() {
  useEffect(() => {
    getapi(api_url);
  }, []);
  const [quote, setQuote] = useState({
    quote: "Click to get quote",
    author: "Unknown"
  });
  const api_url = "https://type.fit/api/quotes";
  let max = 10;
  let min = 0;

  const getapi = async (url) => {
    let response = await fetch(url);
    let data = await response.json();
    max = data.length - 1;

    let index = Math.floor(Math.random() * (max - min) + min);
    setQuote({
      quote: data[index].text,
      author: data[index].author
    });
    const randomColor = Math.floor(Math.random() * 16777215).toString(16);
    document.body.style.backgroundColor = "#" + randomColor;
  };
  const handleApi = () => {
    getapi(api_url);
  };

  return (
    <div
      className="container  text-center bg-primary-subtle "
      style={{ width: "100%", backgroundColor: "red" }}
    >
      <div className="row justify-content-center">
        <div className="col-4">
          <div className="position-absolute top-50 start-50 translate-middle">
            <div className="card" id="quote-box">
              <div className="card-body">
                <blockquote className="blockquote mb-0">
                  <p id="text">{quote.quote}</p>
                  <footer className="blockquote-footer" id="author">
                    <cite title="Source Title">{quote.author}</cite>
                  </footer>
                  <div className="row">
                    <div className="col">
                      <a
                        href="https://twitter.com/intent/tweet?text="
                        id="tweet-quote"
                        target="_blank"
                        rel="noreferrer"
                      >
                        <button id="tweet-design" className="btn btn-primary">
                          <i class="bi bi-twitter"></i>tweet
                        </button>
                      </a>
                    </div>
                    <div className="col">
                      <button
                        type="button"
                        className="btn btn-primary"
                        onClick={handleApi}
                        id="new-quote"
                      >
                        New Quote
                      </button>
                    </div>
                  </div>
                </blockquote>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
